import {Table,Button, } from 'antd'
import "./styles.css";

const RisksData = () => {
    let risks = JSON.parse(localStorage.getItem('risks'));
    const columns = [
        {
            title: ' ',
            dataIndex: 'typeRisk',
            key: 'typeRisk',
          },
        {
          title: '  ',
          dataIndex: 'area_risk_occurrence',
          key: 'area_risk_occurrence',
        },
      
        {
          title: ' ',
          dataIndex: 'kindsRisk',
          key: 'kindsRisk',
        },
        {
            title: '  (   0-10)',
            dataIndex: 'degreeOfRisk',
            key: 'degreeOfRisk',
          },
          {
            title: '  ',
            dataIndex: 'riskManagementTool',
            key: 'riskManagementTool',
          },
      ];
      const clearData = ()=>{
        localStorage.removeItem('risks');
        window.location.reload(false);
      }
  return (
    <div>
      <div className='titleWrapper'><h1></h1><div><Button type="primary" onClick={clearData} disabled={!risks}>  </Button></div></div>
      <Table dataSource={risks} columns={columns} />
    </div>
  );
};
export default RisksData;